import { BehaviorSubject } from 'rxjs/Rx';
import { ErrorHandler } from './../../../handlers/errorHandler';
import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { APPCONFIG } from './../../config';
import { ResidenteProvider } from './../../../providers/residente-provider';
import { Component } from '@angular/core';
import { Router } from '@angular/router';
var _ = require('lodash');
@Component({
    selector: 'residentes',
    template: require('./residentes.component.html'),
})
export class ResidentesComponent {
    private hasRecords;
    private filterRecords = 'Activos';
    private filterDataSource;
    private sourceChangesEmitter = new BehaviorSubject(null);
    private dataSource;
    private dataSet;
    private dataColumn;
    constructor(
        private router: Router,
        private eeloading: EELoading,
        private errorHandler: ErrorHandler,
        private eeToast: EEToast,
        private residenteProvider: ResidenteProvider,
        private dataStg: DataStorageProvider) {
        this.getResidentes();
    }

    ngOnInit() {
        this.dataColumn = [
            {
                key: 'nombre',
                title: 'Nombre'
            },
            {
                key: 'obraSocial',
                title: 'Obra Social'
            },
            {
                key: 'fechaIngreso',
                title: 'Fecha Ingreso'
            },
            {
                key: 'estadia',
                title: 'Estadia'
            },
            {
                key: 'activo',
                title: 'Activo'
            }];
    }

    getResidentes() {
        this.eeloading.open();
        this.residenteProvider
            .GetResidentesByStatus(APPCONFIG.brand.id, 'all')
            .subscribe(
            (data: any) => {
                this.eeloading.close();
                this.dataSource = [];
                this.hasRecords = data.length > 0;
                data.forEach(element => {
                    let dataElement = {
                        residenteID: element.residenteID,
                        nombre: element.nombre,
                        documento: element.tipoDocumento + ' ' + element.numeroDocumento,
                        fechaNacimiento: element.fechaNacimiento.split('T')[0],
                        obraSocial: element.obraSocial,
                        fechaIngreso: element.fechaIngreso.split('T')[0],
                        estadia: (element.activo) ? element.residenciaMatriz.nombre : '',
                        activo: (element.activo) ? 'Si' : 'No'
                    }
                    this.dataSource.push(dataElement);
                });
                this.filterDataSource = this.dataSource;
                this.sourceChangesEmitter.next(this.dataSource)
            },
            error => {
                this.eeloading.close();
                this.errorHandler.handleError(error);
            });

    }

    filterData() {
        this.filterDataSource = [];
        if (this.filterRecords === 'Activos') {
            this.filterDataSource = _.filter(this.dataSource, function (o) { return o.activo === 'Si'; });
            this.sourceChangesEmitter.next(this.filterDataSource);
        } else if (this.filterRecords === 'Inactivos') {
            this.filterDataSource = _.filter(this.dataSource, function (o) { return o.activo === 'No'; });
            this.sourceChangesEmitter.next(this.filterDataSource);
        } else {
            this.sourceChangesEmitter.next(this.dataSource);
        }
    }
    onAddResidente() {
        this.router.navigate(['/app/residente-detalle']);
    }

    onEditResidente(obj) {
        this.router.navigate(['/app/residente-detalle'], { queryParams: { id: obj.residenteID } });
    }

    onDeleteResidente(obj) {
        this.router.navigate(['/app/residente-detalle'], { queryParams: { id: obj.residenteID, action: 'd' } });
    }


}



